const ParralaxContainer = styled.div`
  position: relative;
  height: 100vh;
  & ::after {
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: url("https://placekitten.com/g/900/700");

    transform: translateZ(-1px) scale(1.5);

    background-size: 100%;

    z-index: -1;
  }
`;

const ParallaxBaseLayer = styled.div`
  background: dodgerblue;
  color: white;
  margin-top: -100px;
`;

const Parallax = ({ children }) => (
  <ParralaxContainer>
    <ParallaxBaseLayer>{children}</ParallaxBaseLayer>
  </ParralaxContainer>
);
export default Parallax;

